/* ==========================================================================
   WooCommerce Onsale and Out of Stock responsive styles - begin
   ========================================================================== */

@include laptop-landscape-large {

	.mkdf-woo-single-page {

		&.mkdf-woo-single-thumb-on-left-side {

			.mkdf-single-product-content {

				.mkdf-onsale {
					left: 130px;
				}
			}
		}
	}
}

@include laptop-landscape {

	.mkdf-woo-single-page {

		&.mkdf-woo-single-thumb-on-left-side {

			.mkdf-single-product-content {

				.mkdf-onsale {
					left: 110px;
				}
			}
		}
	}
}

@include ipad-landscape {

    .mkdf-woo-single-page {

		&.mkdf-woo-single-thumb-on-left-side {

			.mkdf-single-product-content {

				.mkdf-onsale {
					left: 170px;
				}
			}
		}
	
	    .mkdf-single-product-content {
		
		    .mkdf-out-of-stock {
			    right: 0;
		    }
	    }
    }
}

@include ipad-portrait {
	
	.mkdf-woo-single-page {
		
		&.mkdf-woo-single-thumb-on-left-side {
			
			.mkdf-single-product-content {
				
				.mkdf-onsale {
					left: 20px;
				}
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Onsale and Out of Stock responsive styles - end
   ========================================================================== */